import React from 'react'
import heart from './icons/heart.svg'
import dots from './icons/dots.svg'

const TrackBtnList = () => {
  return (
    <div className='act_area'>
    <div className='btn_list'>
        <div className='act_btn_playlist'>
                <i className='fa fa-play-circle fa-5x collection_btn'></i>
        </div>
        <div className='act_btn_playlist likedbtn'>
            <img src={heart} alt="" />
        </div>
        <div className='act_btn_playlist editbtn'>
            <img src={dots} alt="" />
        </div>
    </div>
    </div>
  )
}

export default TrackBtnList;