import PlaylistInfo from '../components/UI/PlaylistComs/PlaylistInfo';
import '../static/Styles/collection.css'
import { useParams, useLocation } from 'react-router-dom';
import { useQuery } from 'react-query';
import APIservice from '../API/APIservice';
import TrackList from '../components/UI/PlaylistComs/TrackList';
import TrackBtnList from '../components/UI/TrackComs/TrackBtnList';
import TrackInfo from '../components/UI/TrackComs/PlaylistInfo';
import ArtistBlock from '../components/UI/TrackComs/ArtistBlock';

function TrackPage() {
    const {state} = useLocation()
    const {data, isLoading} = useQuery(`Track-${state.uni_id}`, ()=>{let b = APIservice.getTrack(state.id); return b;})
    if(isLoading){
        return (<h1>loading</h1>)
    }
  return (
    <div>
        <TrackInfo data={data}/>
        <TrackBtnList/>
        <ArtistBlock data={data}/>
    </div>
  );
}
export default TrackPage;