import axios from "axios"
const Domain = window.location.hostname;
export default class APIservice{
    static async getPlaylistList() {
        return axios.get(`${Domain}/api/list`).then(response=>{return response.data})
            
    }
    static async getPlaylist(id) {
        return axios.get(`${Domain}/api/playlist/${id}`).then(response=>{return response.data})  
    }
    static async getTrack(id) {
        return axios.get(`${Domain}/api/track/${id}`).then(response=>{return response.data})  
    }
    static async getArtist(id) {
        return axios.get(`${Domain}/api/artist/${id}`).then(response=>{return response.data})  
    }

}