import React from "react";
import { useContext } from "react";
import ReactJkMusicPlayer from 'react-jinke-music-player'
import 'react-jinke-music-player/assets/index.css'
import { TrackListContext } from "../Context/TrackListContext";
const ResponsivePlayer = ({handleInfo})=> {
    const fothis = ()=>{
        console.log('modechanged');
    }
    var audioInstance = null;
    const {tracks, setTracks} = useContext(TrackListContext);
    return (
           <div>
                <ReactJkMusicPlayer glassBg={true} audioLists={tracks}  theme={'dark'} showDownload={false} showThemeSwitch={false}
                onModeChange={()=>{fothis()}} drag={false} className="details" defaultPosition={{bottom:'50px'}}
                onAudioPlayTrackChange={(currentPlayId,audioLists,audioInfo)=>{console.log('hello'); handleInfo(audioInfo)}}
                autoPlay={false} onAudioListsChange={(currentPlayId,audioLists,audioInf)=>{}} clearPriorAudioLists={true}
                autoPlayInitLoadPlayList={true} restartCurrentOnPrev={true} 
                getAudioInstance={(instance)=>{audioInstance=instance}}/>
           </div>
    )
}

export default ResponsivePlayer;

