import React from 'react'
import logo from '../logo.svg';

import { useState } from 'react'
import '../static/Styles/player_bar.css'
import ResponsivePlayer from './player';
import handleMode from '../utils/PlayerUtil';
const PlayerBar = () => {
    const [trackInfo, setTrackInfo] = useState({name:'track', artist: 'Artist'})
    const [mode, setMode] =  useState('mini')
    const [open, setOpen] = useState(false);
    const PlayPause = (e)=>{
        e.stopPropagation()
    }
  return (
    <div className='player'>
        <div className='wrapper'>
            <div className={open?'details details-open':'details details-close'}>
                <div className={open?'details_inner details-inner-open':'details_inner'}>
                    <div  className={open?'details-inner-img-container details-inner-img-container-open':'details-inner-img-container'} >
                        <div>
                            <img className={open?'music_thumnail_img music_thumnail_img_open':'music_thumnail_img'}   src={logo} alt="" />
                        </div>
                    </div>
                    <div className={open?'track-info track-info-open':'track-info'}>
                         <div>
                            <div className={open?'track-name track-name-open':'track-name'}>
                                {trackInfo.name}
                            </div>
                            <div className={open?'track-artist track-artist-open':'track-artist'}>
                                {trackInfo.artist}
                            </div>
                         </div>
                    </div>
                    {open?<></>
                    :<div className='play-pause-btn'>
                    <i className='fa fa-play fa-5x' onClick={(e)=>{PlayPause(e)}}></i>
                </div>}
                </div>
            </div>
        </div>
        <ResponsivePlayer handleInfo={setTrackInfo}/>
    </div>
    
  )
}

export default PlayerBar